.nav-link-hover {
    position: relative;
}

.nav-link-hover b::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 50%;
    background-color: #000;
    box-shadow: inset 0 -1px 0 #000;
    transition: width .3s ease, left .3s ease;
}

.nav-link-hover:hover b::after {
    width: 100%;
    left: 0;
}
